require('../css/common.css');

// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from '@/store/index'
//引入we-vue 框架
import WeVue from 'we-vue'
import 'we-vue/lib/style.css'
Vue.use(WeVue)

//引入iconfonts(文字图标)
import '../iconfonts/iconfont.css'

//axios请求方式
import axios from 'axios'
Vue.prototype.$axios=axios;

Vue.config.productionTip = false


//过滤器，所有请求前调用，next返回response处理
axios.interceptors.request.use(
    (config) => {
    	if (localStorage.qhwsc_token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
          config.headers.Authorization = localStorage.qhwsc_token;
        }else{
        	//这里如果检测到没有token的话就要跳授权页面重新获取token
        }
		return config;
    },err => {
        return Promise.reject(err);
    }
);



// http response 响应拦截器
import { Toast } from 'we-vue'     //纯文本提示组件
axios.interceptors.response.use(data => {
	if(data.data.status == 0){
		Toast({
  		duration: 2000,
  		message: data.data.msg ? data.data.msg : '服务器异常，请稍后重试！',
  		type: 'text'
		})
	}
	return data;
}, error => {
	return Promise.reject(error)
})

console.log(9999)
/* 自适应布局 */
window.onresize = function dpr() {
    var d = document;
    // psd 设计稿宽度
    var desWidth = 750;
    var _dpr = (1 / window.devicePixelRatio);
    _dpr = 1;
    // 414为iPhone6p的设备独立像素
    var _MaxWidth = 414 * window.devicePixelRatio;
    var userAgent = navigator.userAgent;
    // 设备宽度
    var widthStr = 'device-width';
    // 是否移动设备
    var isMobile = true;
    var iWidth = 0;
    var _html = d.getElementsByTagName('html')[0];
    // 非移动设备
    if (userAgent.toLowerCase().indexOf('iphone') == -1 && userAgent.toLowerCase().indexOf('android') == -1) {
        isMobile = false; //iWidth = _MaxWidth;
        widthStr = iWidth + 'px';
    }
    d.querySelector('[name="viewport"]').setAttribute('content', 'width=' + widthStr + ' , initial-scale=' + _dpr + ', maximum-scale=' + _dpr + ', minimum-scale=' + _dpr + ', user-scalable=no');
    iWidth = Math.min(d.documentElement.clientWidth, window.innerWidth);
    if (!isMobile) iWidth = _MaxWidth;
    _html.style.fontSize = (((100 * iWidth) / desWidth)) + 'px';
    _html.dataset.dpr = 1; // window.devicePixelRatio;
};
window.onresize();


