/**
    @name: '域名接口相关配置'
    @author: 'LLW'
    @time: '2018.04.26'
 */
const domainConfig = {
    'wsc.qinwell.com': {
        url: 'http://wsc.qinwell.com/restapi'
    },
    'wsc.test.qinwell.com': {
        url: 'http://wsc.test.qinwell.com/restapi'
    }
}[window.location.host.toLowerCase()] || {
    url: 'http://wsc.test.qinwell.com/restapi'
};

//console.log(`${domainConfig.url}`)
export default{
	//共用的一些接口
	common:{
		//用户信息
		userDetail:`${domainConfig.url}/user/detail`
	},
	/*订单模块接口*/
	order:{
		//订单列表接口
		list:`${domainConfig.url}/order/list`,
		//订单详情接口
		detail:`${domainConfig.url}/order/detail`,
		//订单提交接口
		commit:`${domainConfig.url}/order/commit`,
	},
	/*商品模块接口*/
	product:{
		//商品列表接口
		list:`${domainConfig.url}/product/list`,
	}
}




